import * as jsLogger from 'js-logger';
import { LogLevel } from '../Types';

const NAME = 'oneplayer';

const toLoggerLevel = [
    jsLogger.OFF,
    jsLogger.ERROR,
    jsLogger.WARN,
    jsLogger.INFO,
    jsLogger.DEBUG
];

const toLogLevel: LogLevel[] = [];
toLogLevel[jsLogger.OFF.value] = LogLevel.NONE;
toLogLevel[jsLogger.ERROR.value] = LogLevel.ERROR;
toLogLevel[jsLogger.WARN.value] = LogLevel.WARN;
toLogLevel[jsLogger.INFO.value] = LogLevel.INFO;
toLogLevel[jsLogger.DEBUG.value] = LogLevel.DEBUG;


export class Logger {

    private static getLogger() {
        return jsLogger.get(NAME);
    }

    public static init () {
        jsLogger.useDefaults();
        this.getLogger().setLevel(jsLogger.OFF);
    }

    public static getLevel (): LogLevel {
        return toLogLevel[this.getLogger().getLevel().value];
    }

    public static setLevel (level: LogLevel) {
        this.getLogger().setLevel(toLoggerLevel[level]);
    }

    public static error (...args: any[]) {
        this.getLogger().error.apply(this.getLogger(), args);
    }

    public static warn (...args: any[]) {
        this.getLogger().warn.apply(this.getLogger(), args);
    }

    public static info (...args: any[]) {
        this.getLogger().info.apply(this.getLogger(), args);
    }

    public static debug (...args: any[]) {
        this.getLogger().debug.apply(this.getLogger(), args);
    }
}